nav {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 80px;
}
.container {
  width: 800px;
  margin: 0 auto;
  background-color: transparent;
}
.links {
  background-color: rgb(55, 53, 47);
  font-family: "Courier Prime", monospace;
  color: #fff;
  padding: 10px;
  border: none;
  text-align: center;
  text-decoration: none;
  font-size: 13px;
}
.button {
  border-radius: 36px;
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
}
.links:visited {
  background-color: rgb(55, 53, 47);
  text-decoration: none;
}
.links:hover {
  background-color: rgb(89, 89, 89);
  text-decoration: none;
  /* background-color: rgb(140, 136, 115); */
}
.links:active {
  background-color: black;
  text-decoration: none;
}
